import React, { Component } from 'react';
import { Formik, FormikProps, Form, Field, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import {
  Badge,
  Button,
  ButtonDropdown,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';
import { getUserRole } from '../Apis';



class Profile extends Component {
    constructor(props) {
        super(props);
        this.state = {
            first_name:'',
            last_name:'',
            short_name:'',
            email:'',
            role:'',
            phone:'',
            phone_alt:'',
            gender:''
        };
      }

        componentDidMount=async()=> {
            if(localStorage.getItem('id')!==undefined||localStorage.getItem('id')!==null)
            {
                const responseData= await getUserRole(localStorage.getItem('id'));
                console.log(responseData,'response')
                  if(responseData.status == 200){
                    if(responseData.data.user.first_name)
                    {
                        this.setState({
                            first_name:responseData.data.user.first_name,
                            last_name:responseData.data.user.last_name,
                            short_name:responseData.data.user.short_name,
                            email:responseData.data.user.email,
                            role:responseData.data.roles.length!==0&&responseData.data.roles[0].name,
                            phone:responseData.data.user.phone,
                            gender:responseData.data.user.gender==1?'Male':'Female',
                            phone_alt:responseData.data.user.alternate_phone,
                            userImage:responseData.data.patientImage.value
                        })
                    }
                  } else if (responseData.status == 404) {
                    console.warn(responseData.data.message);
                  } 
           
            }
    }
    render() {
       console.log(this.state.userImage)
        return(
            <div className="animated fadeIn">
                <Row>
                <Col xs="2">
                </Col>
                    <Col xs="8">
                    <Card>
                        <CardHeader>
                            <strong>My profile</strong>
                        </CardHeader>
                        <CardBody> 
                        <Row>
                            <Col md="2">
                                <img className="dpimg" src={this.state.userImage!=null||this.state.userImage!=undefined?'http://iotapp.ephlux.com:3000'+this.state.userImage:require('./../../assets/img/5.jpg')}/>
                            </Col>
                            <Col xs="12" md="9" >
                                <p className="profileHeading">{this.state.first_name + ' '+this.state.last_name}</p>
                            </Col>
                        </Row> 
                       <Row>
                            <Col md="3">
                                <p >First Name</p>
                            </Col>
                            <Col xs="12" md="9">
                                <p >{this.state.first_name}</p>
                            </Col>
                        </Row> 
                        <Row>
                            <Col md="3">
                                <p>Last Name</p>
                            </Col>
                            <Col xs="12" md="9">
                                <p >{this.state.last_name}</p>
                            </Col>
                        </Row>   
                        <Row>
                            <Col md="3">
                                <p>Short Name</p>
                            </Col>
                            <Col xs="12" md="9">
                                <p >{this.state.short_name}</p>
                            </Col>
                        </Row>    
                        <Row>
                            <Col md="3">
                                <p >Email</p>
                            </Col>
                            <Col xs="12" md="9">
                                <p >{this.state.email}</p>
                            </Col>
                        </Row> 
                        <Row>
                            <Col md="3">
                                <p >Gender</p>
                            </Col>
                            <Col xs="12" md="9">
                                <p >{this.state.gender}</p>
                            </Col>
                        </Row> 
                        <Row>
                            <Col md="3">
                                <p>Phone</p>
                            </Col>
                            <Col xs="12" md="9">
                                <p >{this.state.phone}</p>
                            </Col>
                        </Row>     
                        <Row>
                            <Col md="3">
                                <p>Alternate Phone</p>
                            </Col>
                            <Col xs="12" md="9">
                                <p >{this.state.phone_alt}</p>
                            </Col>
                        </Row> 
                        <Row>
                            <Col md="3">
                                <p>Role</p>
                            </Col>
                            <Col xs="12" md="9">
                                <p >{this.state.role}</p>
                            </Col>
                        </Row>  
                        </CardBody>
                    </Card>
                    </Col>
                </Row>
            </div>
            
        );
    }

}

export default Profile;